// Styling for filter options
.filter {
	clear: fix;
	margin-top: 30px;
	padding-bottom: 20px;
	border-bottom: 1px solid $default-color;
}

.filter-month {
	clear: fix;
	margin-top: 10px;
	padding-bottom: 30px;

	ol {
		margin: 0;
	}

	.year {
		clear: both;
		float: left;
		padding-right: 10px;
		margin: 3px 0;
	}
}
