.social-links {
	margin-top: -10px;

	a {
		font-size: 1.8em;
		float: right;
		overflow: hidden;
		margin-left: 5px;
		width: 1.5em;
		height: 1.5em;

		i {
			display: block;
			position: relative;
			height: 100%;
			&:before {
				position: absolute;
				top: 0.3em;
				left: 0.3em;
			}
		}
	}
}
